import Vue from 'vue' // vue
import Router from 'vue-router' // 

const global = resolve => require(['@/components/sinda_global'], resolve);
const LoginRegister = resolve => require(['../components/sinda_LoginRegister'], resolve);
const index = resolve => require(['../views/sinda_index'], resolve);
const list = resolve => require(['../views/sinda_list'], resolve);
const shop = resolve => require(['../views/sinda_shop'], resolve);
// import global from '@/components/sinda_global' // 
// import LoginRegister from '../components/sinda_LoginRegister' // 
// import index from '../views/sinda_index' // 
// import list from '../views/sinda_list' // 
// import shop from '../views/sinda_shop' // 


const service = resolve => require(['../views/sinda_shop/sinda_service'], resolve);
const product = resolve => require(['../views/sinda_shop/sinda_product'], resolve);
const certificate = resolve => require(['../views/sinda_shop/sinda_certificate'], resolve);
// import service from '../views/sinda_shop/sinda_service' // 
// import product from '../views/sinda_shop/sinda_product' // 
// import certificate from '../views/sinda_shop/sinda_certificate' // 

const login = resolve => require(['../views/sinda_login'], resolve);
const register = resolve => require(['../views/sinda_register'], resolve);
const line_item = resolve => require(['../stores/line_item'], resolve);
const payment = resolve => require(['../views/sinda_payment'], resolve);
// import login from '../views/sinda_login' //
// import register from '../views/sinda_register' //
// import line_item from '../stores/line_item' //
// import payment from '../views/sinda_payment' //


const payment_success = resolve => require(['../views/sinda_payment/sinda_payment_success'], resolve);
const payment_failure = resolve => require(['../views/sinda_payment/sinda_payment_failure'], resolve);
// import payment_success from '../views/sinda_payment/sinda_payment_success' //
// import payment_failure from '../views/sinda_payment/sinda_payment_failure' //


const member = resolve => require(['../views/sinda_member'], resolve);
const myYing = resolve => require(['../member-third/myYing'], resolve);
const center = resolve => require(['../member-third/center'], resolve);
const toeveluete = resolve => require(['../member-third/toeveluete'], resolve);
const evalu = resolve => require(['../member-third/evalu'], resolve);
const setting = resolve => require(['../member-third/setting'], resolve);
const modify = resolve => require(['../member-third/modify'], resolve);
// import member from '../views/sinda_member' //
// import myYing from '../member-third/myYing' //
// import center from '../member-third/center' //
// import toeveluete from '../member-third/toeveluete' //
// import evalu from '../member-third/evalu' //
// import setting from '../member-third/setting' //
// import modify from '../member-third/modify' //


const Join_Us = resolve => require(['../stores/Join_Us'], resolve);
const store_list = resolve => require(['../stores/store_list'], resolve);
const forgetPs = resolve => require(['../views/sinda_forgetPs.vue'], resolve);
const tax = resolve => require(['../views/sinda_list/sinda_taxServer'], resolve);
const cart = resolve => require(['../views/sinda_list/sinda_cart'], resolve);
// import Join_Us from '../stores/Join_Us' //
// import store_list from '../stores/store_list' //
// import forgetPs from '../views/sinda_forgetPs.vue' //
// import tax from '../views/sinda_list/sinda_taxServer' //
// import cart from '../views/sinda_list/sinda_cart' //
// import pay from '../views/' //

const pro = resolve => require(['../views/sinda_list/sinda_products'], resolve);
const com = resolve => require(['../views/sinda_list/company'], resolve);
const list_list = resolve => require(['../views/sinda_list/list_list'], resolve);
// import pro from '../views/sinda_list/sinda_products' //
// import com from '../views/sinda_list/company' //
// import list_list from '../views/sinda_list/list_list' //
import {
    MessageBox
} from "mint-ui";

// -----------------------------------------------------------------------------------------------------------------------
// 
import m_global from '../components/m_global' //
import m_index from '../m_sinda/m_index' //
import m_classify from '../m_sinda/m_classify' //
import m_success from '../m_sinda/m_success' //
import m_failure from '../m_sinda/m_failure' //
import m_shop from '../m_sinda/m_shop' //shop
import m_storelist from '../m_sinda/m_storelist' //
import m_joinus from '../m_sinda/m_joinus' //
import m_lineitem from '../m_sinda/m_lineitem' //
import m_shop_service from '../m_sinda/m_shop/m_shop_service' //shop service
import m_out from '../components/m_out' //
import generic from '../components/generic' //
import logged from '../components/logged' //
import shoppingCard from '../components/shoppingCard' //
import listPage from '../components/listPage' //
import shoppingstalley from '../components/shoppingstalley.vue' //
import set from '../components/set' //
import myOrder from '../components/myOrder' //
import registerP from '../components/registerP' //
import m_details from '../m_sinda/m_details' //
import loginP from '../components/loginP' //
import cipher from '../components/cipher' //
Vue.use(Router)


// pc
// 
// globalloginregister

// global
// indexlistloginregister
//  views
//   

// , {
//     
export default new Router({
    routes: [{
            path: '/',
            name: 'sinda_global',
            component: global,
            redirect: 'index',
            children: [{
                    path: 'index', // path      http://localhost:8081/#/
                    component: index
                }, {
                    path: 'list', //     http://localhost:8081/#/list
                    component: list,
                    children: [{
                        path: 'tax', //    http://localhost:8081/#/list/tax
                        component: tax
                    }, {
                        path: 'pro', //    http://localhost:8081/#/list/cart
                        component: pro
                    }, {
                        path: 'cart', //    http://localhost:8081/#/list/cart
                        component: cart
                    }, {
                        path: 'com', //    http://localhost:8081/#/list/cart
                        component: com
                    }, {
                        path: 'list', //    http://localhost:8081/#/list/cart
                        component: list_list
                    }]
                }, {
                    path: 'Join_Us', //   http://localhost:8081/#/Join_Us
                    component: Join_Us
                }, {
                    path: 'line_item', //   http://localhost:8081/#/line_item
                    component: line_item
                }, {
                    path: 'store_list', //  http://localhost:8081/#/store_list
                    component: store_list
                },

                {
                    path: '/shop',
                    component: shop,
                    redirect: "/shop/service", //  http://localhost:8080/#/shop
                    children: [{
                        path: 'service',
                        component: service,
                    }, {
                        path: 'product',
                        component: product, //
                    }, {
                        path: 'certificate',
                        component: certificate, //
                    }]

                }, {
                    path: 'payment',
                    component: payment,
                    children: [{
                        path: 'success', //  http://localhost:8080/#/payment/success
                        component: payment_success,
                    }, {
                        path: 'failure', // http://localhost:8080/#/payment/failure
                        component: payment_failure,
                    }]
                }, {
                    path: 'member',
                    component: member,
                    children: [{
                            path: 'myYing', //    http://localhost:8081/#/member/myYing
                            component: myYing
                        },
                        {
                            path: 'center', //    http://localhost:8081/#/member/center
                            component: center
                        },
                        {
                            path: 'toeveluete', //    http://localhost:8081/#/member/toeveluete
                            component: toeveluete
                        },
                        {
                            path: 'setting', //    http://localhost:8081/#/member/setting
                            component: setting
                        },
                        {
                            path: 'evalu', //    http://localhost:8081/#/member/evalu
                            component: evalu
                        },
                        {
                            path: 'modify', //    http://localhost:8081/#/member/modify
                            component: modify
                        }
                    ]
                }
            ]
        }, {
            path: '/LoginRegister',
            name: 'sinda_LoginRegister',
            component: LoginRegister,
            children: [{
                    path: 'login',
                    component: login
                },
                {
                    path: 'register',
                    component: register
                },
                {
                    path: 'forgetPs',
                    component: forgetPs
                },
            ]
        }, {
            path: '/m.sinda', //  
            component: m_global,
            children: [{
                    path: '/', // 
                    component: m_index
                }, {
                    path: '/classify', // 
                    component: m_classify
                }, {

                    path: '/shoppingCard', //
                    name: 'shoppingCard',
                    component: shoppingCard
                }, {
                    path: '/generic', //
                    name: 'generic',
                    component: generic
                },{
                    path: '/logged', //
                    name: 'logged',
                    component: logged
                }, {
                    path: '/m_storelist', // http://localhost:8081/#/m.out/m_stprelist
                    component: m_storelist
                }, {
                    path: 'shop', // 
                    component: m_shop,
                    redirect: "shop/service",
                    children: [{
                        path: 'service',
                        component: m_shop_service,
                    }]
                }, {
                    path: '/m.out', //  
                    name: 'm.out',
                    component: m_out,
                    children: [{
                            path: 'success', // 
                            name: 'm_success',
                            component: m_success
                        }, {
                            path: 'failure', // 
                            name: 'm_failure',
                            component: m_failure
                        }]
                        // children: [{}]
                }, {
                    path: '/myOrder', //
                    name: 'myOrder',
                    component: myOrder
                }, {
                    path: '/listPage', //
                    name: 'listPage',
                    component: listPage
                }, {
                    path: '/set', //
                    name: 'set',
                    component: set
                }, 

            ]
        }, {
            path: '/m_joinus', // http://locahost:8801/#/m_joinus
            component: m_joinus,
        }, {
            path: '/m_lineitem', // http://locahost:8801/#/m_lineitem
            component: m_lineitem
        },{
            path: '/m_joinus', // http://locahost:8801/#/m_joinus
            component: m_joinus,
        }, {
            path: '/m_lineitem', // http://locahost:8801/#/m_lineitem
            component: m_lineitem
        }, {
            path: '/details', //
            name: 'details',
            component: m_details
        }, {
            path: '/registerP', //
            name: 'registerP',
            component: registerP
        }, {
            path: '/loginP', //
            name: 'loginP',
            component: loginP
        }, {
            path: '/cipher', //
            name: 'cipher',
            component: cipher
        }
    ]
})


