import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import qs from 'qs'

Vue.prototype.qs = qs;

Vue.use(Vuex);
export default new Vuex.Store({
  state: { //
    num: 0,
    loginState: '',
    listName: 'listName',
    userName: '',
    toeva: '', //
  },
  mutations: { //
    SET_NUM(state, num) { // 
      axios.post("/xinda-api/cart/cart-num").then(data => {
        if (data.data.status == 1) {
          state.num = (data.data.data.cartNum);
        }
      });
    },
    SET_LOGIN_STATE(state, loginState) { // 
      state.loginState = loginState;
    },
    SET_LIST_NAME(state, listName) { // 
      state.listName = listName;
    },
    SET_USER_NAME(state, userName) { // 
      state.userName = userName;
    },
    SET_NUM_TOEVA(state, toeva) { //
      state.toeva = toeva;
    }
  },

  actions: { //
    setNum({ // 
      commit
    }, num) {
      commit('SET_NUM', num);
    },
    setloginState({ // 
      commit
    }, loginState) {
      commit('SET_LOGIN_STATE', loginState);
    },
    setlistName({
      commit
    }, listName) {
      commit('SET_LIST_NAME', listName);
    },
    setuserName({
      commit
    }, userName) {
      commit('SET_USER_NAME', userName);
    },
    setnumtoeva({ //
      commit
    }, toeva) {
      commit('SET_NUM_TOEVA', toeva)
    }
  },
  getters: { //
    getNum: function(state) {
      return state.num
    },
    getloginState: state => state.loginState,
    getlistName: state => state.listName,
    getuserName: state => {
      if (state.userName) {
        return state.userName
      } else {
        return sessionStorage.getItem("userName");
      }
    },
    setnumtoeva: state => state.toeva
  }
});



